import { uniAppHook, Global } from '../helpers/config';
import {
    callAppHook, getPages, getPageVmOrMp, ruleToUniNavInfo, formatTo, formatFrom, APPGetPageRoute, getPageOnBeforeBack,
} from './util';
import { noop } from '../helpers/util';
import { warn } from '../helpers/warn';
import uniPushTo from './uniNav';

let startBack = false;	//  

/**
 *   app.vue  index 
 * @param {Boolean} callHome // 
 *
 * this  page 
 */
const callwaitHooks = function (callHome) {
    return new Promise(async (resolve) => {
        const variation = [];	// uni-app  
        const {
            appVue, indexVue, onLaunch, onShow, waitHooks, variationFuns, indexCallHooks,
        } = uniAppHook;
        const app = appVue.$options;
        await onLaunch.fun[onLaunch.fun.length - 1].call(appVue, onLaunch.args);	//  
        onShow.fun[onShow.fun.length - 1].call(appVue, onShow.args);	// onshow  
        if (callHome) {	// 
            // eslint-disable-next-line
            for (const key in waitHooks) {
                if (indexCallHooks.includes(key)) {	// 
                    callAppHook.call(this, waitHooks[key].fun);
                }
            }
        }
        if (onLaunch.isHijack) {	//  onLaunch
            app.onLaunch.splice(app.onLaunch.length - 1, 1, onLaunch.fun[0]);
        }
        if (onShow.isHijack) {	//  onShow
            app.onShow.splice(app.onShow.length - 1, 1, onShow.fun[0]);
        }
        // eslint-disable-next-line
        for (const key in waitHooks) {	//  
            const item = waitHooks[key];
            if (item.isHijack) {
                if (variationFuns.includes(key)) {	// 
                    variation.push({ key, fun: item.fun[0] });
                } else {
                    const indeHooks = indexVue[key];
                    //  https://github.com/SilurianYang/uni-simple-router/issues/76
                    setTimeout(() => {	//   uni-app 
                        indeHooks.splice(indeHooks.length - 1, 1, item.fun[0]);
                    }, 50);
                }
            }
        }
        resolve(variation);
    });
};
/**
 * 
 * @param {Object} variation uni-app  
 */
const callVariationHooks = function (variation) {
    for (let i = 0; i < variation.length; i += 1) {
        const { key, fun } = variation[i];
        const indeHooks = uniAppHook.indexVue[key];
        indeHooks.splice(indeHooks.length - 1, 1, fun);
    }
};

/**
 * app.vueonLaunchonShow
 *
 * this  page 
 */
export const proxyLaunchHook = function () {
    const {
        onLaunch,
        onShow,
    } = this.$options;
    uniAppHook.appVue = this;		//  app.vue
    if (onLaunch.length > 1) {	//  onLaunch  
        uniAppHook.onLaunch.isHijack = true;
        uniAppHook.onLaunch.fun = onLaunch.splice(onLaunch.length - 1, 1, (arg) => {
            uniAppHook.onLaunch.args = arg;
        });		// uni-app
    }
    if (onShow.length > 0) {
        uniAppHook.onShow.isHijack = true;
        uniAppHook.onShow.fun = onShow.splice(onShow.length - 1, 1, (arg) => {
            uniAppHook.onShow.args = arg;
            if (uniAppHook.pageReady) {		// app
                callAppHook.call(this, uniAppHook.onShow.fun, arg);
            }
        });	//  
    }
};

/**
 * 
 * this  page 
 */
export const proxyIndexHook = function (Router) {
    const { needHooks, waitHooks } = uniAppHook;
    const options = this.$options;
    uniAppHook.indexVue = options;
    for (let i = 0; i < needHooks.length; i += 1) {
        const key = needHooks[i];
        if (options[key] != null) {	// 
            const { length } = options[key];
            // eslint-disable-next-line
            const whObject= waitHooks[key]={};
            whObject.fun = options[key].splice(length - 1, 1, noop);	// ,
            whObject.isHijack = true;
        }
    }
    // eslint-disable-next-line
    triggerLifeCycle.call(this, Router);	//  
};
/**
 * beforeHooks 
 * @param {Object} _from // from  
 * @param {Object} _to  // to 
 *
 * this  Router 
 */
const beforeHooks = function (_from, _to) {
    return new Promise(async (resolve) => {
        const beforeHooksFun = this.lifeCycle.beforeHooks[0];
        if (beforeHooksFun == null) {
            return resolve();
        }
        await beforeHooksFun.call(this, _to, _from, resolve);
    });
};
/**
 * afterEachHooks 
 * @param {Object} _from // from  
 * @param {Object} _to  // to 
 *
 * this  Router 
 */
const afterEachHooks = function (_from, _to) {
    const afterHooks = this.lifeCycle.afterHooks[0];
    if (afterHooks != null && afterHooks.constructor === Function) {
        afterHooks.call(this, _to, _from);
    }
};
/**
 *  beforeEnter 
 * @param {Object} finalRoute 	// 
 * @param {Object} _from // from  
 * @param {Object} _to  // to 
 *
 * this  Router 
 */
const beforeEnterHooks = function (finalRoute, _from, _to) {
    return new Promise(async (resolve) => {
        const { beforeEnter } = finalRoute.route;
        if (beforeEnter == null || beforeEnter.constructor !== Function) {	// beforeEnter 
            return resolve();
        }
        await beforeEnter.call(this, _to, _from, resolve);
    });
};
/**
 * 
 * @param {Object} page	getPages
 * @param {Object} options 	vue
 * @param {Object} backLayerC	
   *
 * this  Router 
 */
const backCallHook = function (page, options, backLayerC = 1) {
    const route = APPGetPageRoute([page]);
    const NAVTYPE = 'RouterBack';
    // eslint-disable-next-line
    transitionTo.call(this, { path: route.path, query: route.query }, NAVTYPE, (finalRoute, fnType) => {
        if (fnType != NAVTYPE) { // apinext NAVTYPE  
            return uniPushTo(finalRoute, fnType);
        }
        if (startBack) { // 
            return warn('');
        }
        startBack = true;	// 
        options.onBackPress = [noop];	// uni-app
        setTimeout(() => {
            this.back(backLayerC, undefined, true); // 
            startBack = false;	// 
        });
    });
};
/**
 * 
 * @param {Object} options  vue $options
 * @param {Array} args  
 *
 * this  Router 
 */
export const beforeBackHooks = async function (options, args) {
    const isNext = await getPageOnBeforeBack(args); // onBeforeBack
    if (isNext === false) { // onBeforeBack  true 
        Global.LockStatus = false; // 
        return false;
    }
    const page = getPages(-3);	// 
    backCallHook.call(this, page, options);
};
/**
 * back api
 * @param {Object} options  vue $options
 * @param {Array} args  
 *
 * this  Router 
 */
export const backApiCallHook = async function (options, args) {
    await getPageOnBeforeBack(args);
    const { backLayerC } = Global;
    const pages = getPages();
    let page = null;
    if (backLayerC > pages.length - 1 || backLayerC == pages.length - 1) {	//  tabbar
        // eslint-disable-next-line
        page = pages[0];
    } else {
        page = pages[pages.length - 2];
    }
    backCallHook.call(this, page, options, backLayerC);
};
/**
 *  v1.5.4+
 * beforeRouteLeave 
 * @param {Object} to        to
 * @param {Object} from      from
 *  @param {Boolean} leaveHook:?  beforeRouteLeave next   beforeRouteLeave 
 * this  Router 
 */
const beforeRouteLeaveHooks = function (from, to, leaveHook) {
    return new Promise((resolve) => {
        if (leaveHook) { // beforeRouteLeave next
            warn('beforeRouteLeave next');
            return resolve();
        }
        if (from.path == to.path) { // 
            return resolve();
        }
        const currentPage = getPages(-2); // 
        const callThis = getPageVmOrMp(currentPage); //  $vm   pagethis
        const { beforeRouteLeave } = callThis.$options; // 
        if (beforeRouteLeave == null) {
            warn(' beforeRouteLeave ');
            return resolve();
        }
        if (beforeRouteLeave != null && beforeRouteLeave.constructor !== Function) {
            warn('beforeRouteLeave ');
            return resolve();
        }
        beforeRouteLeave.call(callThis, to, from, resolve); // 
    });
};

/**
 *  next() 
 *
 * @param {Object} Intercept 
 * @param {Object} fnType  
 * @param {Object} navCB  
 * @param {Boolean} leaveHookCall:?  beforeRouteLeave next 
 * this  Router 
 *
 */
const isNext = function (Intercept, fnType, navCB, leaveHookCall = false) {
    return new Promise((resolve, reject) => {
        if (Intercept == null) {		//  
            return resolve();
        }
        if (Intercept === false) {		// 
            Global.LockStatus = false; // 
            return reject('');
        }
        if (Intercept.constructor === String) {		//  path  NAVTYPE navType
            reject('next');
            // eslint-disable-next-line
            return transitionTo.call(this, Intercept, fnType, navCB,leaveHookCall);
        }
        if (Intercept.constructor === Object) {	//  
            reject('next');
            // eslint-disable-next-line
            return transitionTo.call(this, Intercept, Intercept.NAVTYPE || fnType, navCB,leaveHookCall);
        }
    });
};
/**
 *  
 * @param {Object} rule 
 * @param {Object} fnType 
 * @param {Object} navCB:? 
 * @param {Boolean} leaveHook:?  beforeRouteLeave next   beforeRouteLeave 
 *
 * this  Router 
 */
export const transitionTo = async function (rule, fnType, navCB, leaveHook = false) {
    await this.lifeCycle.routerbeforeHooks[0].call(this); // 
    const finalRoute = ruleToUniNavInfo(rule, this.CONFIG.routes);		//  route 
    const _from = formatFrom(this.CONFIG.routes);	//  from 
    const _to = formatTo(finalRoute);	//  to 
    try {
        const leaveResult = await beforeRouteLeaveHooks.call(this, _from, _to, leaveHook); //  beforeRouteLeave  v1.5.4+
        await isNext.call(this, leaveResult, fnType, navCB, true);	//      

        const beforeResult = await beforeHooks.call(this, _from, _to);		//  beforeEach 
        await isNext.call(this, beforeResult, fnType, navCB);	//      

        const enterResult = await beforeEnterHooks.call(this, finalRoute, _from, _to);	//  beforeEnter 
        await isNext.call(this, enterResult, fnType, navCB);	//   
    } catch (e) {
        warn(e); // 
        return false;
    }
    if (navCB) {
        navCB.call(this, finalRoute, fnType);	// 
    }
    afterEachHooks.call(this, _from, _to);
    await this.lifeCycle.routerAfterHooks[0].call(this); // 
};
/**
 * 
 * @param {Object} Router 
 *
 * this  vue
 */
export const triggerLifeCycle = function (Router) {
    const topPage = getCurrentPages()[0];
    if (topPage == null) {
        return warn(', bug??');
    }
    const { query, page } = getPageVmOrMp(topPage, false);
    transitionTo.call(Router, { path: page.route, query }, 'push', async (finalRoute, fnType) => {
        let variation = [];
        if (`/${page.route}` == finalRoute.route.path) {		// 
            uniAppHook.pageReady = true;		//  
            await callwaitHooks.call(this, true);
        } else {	// 
            variation = await callwaitHooks.call(this, false);	// app.vue
            await uniPushTo(finalRoute, fnType);
        }
        plus.nativeObj.View.getViewById('router-loadding').close();
        callVariationHooks(variation);
        uniAppHook.pageReady = true;		//  
    });
};

/**
 * tabbar
 * @param {Object} path tab
 *
 * this  Router 
 */
export const beforeTabHooks = function (path) {
    transitionTo.call(this, { path: `/${path}`, query: {} }, 'pushTab', (finalRoute, fnType) => {
        uniPushTo(finalRoute, fnType);
    });
};
