import fly from '../request'


//
export function changeInfo(params) {
	return fly.request({
		url: 'account/updateAccount',
		method: 'get',
		body: params
	})
}

//
export function msgList(params) {
	return fly.request({
		url: 'onlineWm/predict_msg',
		method: 'get',
		body: params
	})
}
//
export function getReportList(params) {
	return fly.request({
		url: 'report/report_deal',
		method: 'get',
		body: params
	})
}















