import { Global } from './config';
import { warn, err } from './warn';

const nodeURL = require('query-string');

class ParseQuery {
    get queryName() {
        return nodeURL;
    }

    /**
     * 
     * @param {Object} obj
     */
    isDepthObject(obj) {
        const str = JSON.stringify(obj);
        return str.match(/}/g).length > 1;
    }

    /**
     * URL
     * @param {String} url
     */
    extract(url) {
        return nodeURL.extract(url);
    }

    /**
     *  key=value&key1=value 
     * @param {string} strQuery key=value&key1=value 
     */
    parse(strQuery) {
        return nodeURL.parse(strQuery);
    }

    /**
     *  key=value&key1=value 
     * @param {Object} ObjQuery js
     * @param {Boolean} intact 
     */
    stringify(ObjQuery, intact = true) {
        const strQuery = nodeURL.stringify(ObjQuery);
        if (intact) {
            return `?${strQuery}`;
        }
        return strQuery;
    }

    /**
     *  key=value&key1=value  query=encodeURIComponent(value)
     * @param {Object|String} query js  key=value&key1=value 
     * @param {Boolean} intact 
     */
    encode(query, intact = true) {
        let [strQuery, formatQuery] = ['', ''];
        if (query == null) {
            warn('', true);
            return '';
        }
        if (query.constructor === String) { //    
            strQuery = JSON.stringify(this.parse(query));
        } else if (query.constructor === Object) { // 
            if (Object.keys(query).length === 0) {
                warn('');
                return '';
            }
            strQuery = JSON.stringify(query);
        }
        if (intact) {
            formatQuery = '?';
        }
        formatQuery += `query=${encodeURIComponent(strQuery)}`;
        return formatQuery;
    }

    /**
     *  query=encodeURIComponent(value)  
     * @param {string} strQuery   query=encodeURIComponent(value)
     */
    decode(strQuery) {
        if (strQuery == null) {
            warn('', true);
            return {};
        }
        let jsonQuery = strQuery;
        if (strQuery.constructor === Object) { //  
            jsonQuery = strQuery.query;
            if (jsonQuery == null) {
                warn('');
                return {};
            }
            jsonQuery = `query=${jsonQuery}`;
        }
        let decode = {};
        // query   query=encodeURIComponent(value)
        const decodeStr = decodeURIComponent(jsonQuery);
        const { query } = this.parse(decodeStr); //  json json
        if (query == null) {
            warn('');
        } else {
            try {
                decode = JSON.parse(query);
            } catch (error) {
                warn('');
            }
        }
        return decode;
    }

    queryGet(query) {
        const { encodeURI } = Global.Router.CONFIG; // 
        let [decode, historyObj, strQuery] = [query, query, ''];
        switch (encodeURI) {
        case true: { // 
            decode = this.decode(query);
            strQuery = this.encode(decode);
            historyObj = {
                query: encodeURIComponent(JSON.stringify(decode)),
            };
            break;
        }
        case false: { // 
            strQuery = this.stringify(query);
            break;
        }
        default: {
            err(' \'encodeURI\'', true);
        }
        }
        return { strQuery, historyObj, decode };
    }


    /**
     * 
     * @param {Object|String} query getfalse  Object 
     * @param {String} get  api
     */
    transfer(query = {}) {
        const { encodeURI } = Global.Router.CONFIG; // 
        switch (encodeURI) {
        case true: {
            // 
            return this.encode(query, false);
        }
        case false: {
            // 
            return this.stringify(query);
        }
        default: {
            err(' \'encodeURI\' ', true);
        }
        }
    }
}

export default ParseQuery;
