import fly from '../request'


//
export function getPatList(params) {
	return fly.request({
		url: 'patrol/list',
		method: 'get',
		body: params
	})
}
//
export function addPatrol(params) {
	return fly.request({
		url: 'patrol/add_patrol_history',
		method: 'post',
		body: params
	})
}

//
export function getDetail(params) {
	return fly.request({
		url: 'patrol/detail',
		method: 'get',
		body: params
	})
}
//
export function updateReport(params) {
	return fly.request({
		url: 'patrol/update',
		method: 'get',
		body: params
	})
}















