import fly from '../request'
export function uploadImg (params) {
    return post('uploadimg', params)
}

//
export function login(params) {
    return fly.request({
        url: 'account/login',
        method: 'post',
        body: params
    })
}
//
export function getNodeList(params) {
	return fly.request({
		url: 'report/node_ids',
		method: 'get',
		body: params
	})
}

//
export function getReportList(params) {
	return fly.request({
		url: 'report/list',
		method: 'get',
		body: params
	})
}
//
export function getReportDetail(params) {
	return fly.request({
		url: 'report/detail',
		method: 'get',
		body: params
	})
}
//
export function addReport(params) {
	return fly.request({
		url: 'report/add',
		method: 'post',
		body: params
	})
}
//
export function updateReport(params) {
	return fly.request({
		url: 'report/update',
		method: 'get',
		body: params
	})
}
//
export function reportDetail(params) {
	return fly.request({
		url: 'report/add_history',
		method: 'post',
		body: params
	})
}

//
export function count(params) {
	return fly.request({
		url: 'account/count',
		method: 'get',
		body: params
	})
}















